'use client';
import { useState, useEffect, useCallback } from 'react';
import toast from 'react-hot-toast';

interface Notification {
    _id: string;
    title: string;
    message: string;
    type: 'like' | 'comment' | 'system' | 'general';
    read: boolean;
    createdAt: string;
    updatedAt: string;
}

export const useNotification = () => {
    const [notifications, setNotifications] = useState<Notification[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    const fetchNotifications = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);
            
            // TODO: Replace with actual API call when backend is ready
            // const response = await api.get('/notifications');
            // setNotifications(response.data);
            
            // Mock data for now
            const mockNotifications: Notification[] = [
                {
                    _id: '1',
                    title: 'Welcome to StudyBuddy!',
                    message: 'Thanks for joining our platform. Start exploring programs and courses.',
                    type: 'system',
                    read: false,
                    createdAt: new Date().toISOString(),
                    updatedAt: new Date().toISOString()
                },
                {
                    _id: '2',
                    title: 'Your review was liked',
                    message: 'Someone liked your review on Computer Science program.',
                    type: 'like',
                    read: true,
                    createdAt: new Date(Date.now() - 24 * 60 * 60 * 1000).toISOString(),
                    updatedAt: new Date(Date.now() - 24 * 60 * 60 * 1000).toISOString()
                }
            ];
            
            setNotifications(mockNotifications);
            
        } catch (err: any) {
            console.error('Error fetching notifications:', err);
            setError('Failed to load notifications');
            toast.error('Failed to load notifications');
        } finally {
            setLoading(false);
        }
    }, []);

    const markAsRead = useCallback(async (notificationId: string) => {
        try {
            // TODO: Replace with actual API call
            // await api.put(`/notifications/${notificationId}/read`);
            
            setNotifications(prev => 
                prev.map(notification => 
                    notification._id === notificationId 
                        ? { ...notification, read: true }
                        : notification
                )
            );
        } catch (err: any) {
            console.error('Error marking notification as read:', err);
            toast.error('Failed to mark notification as read');
        }
    }, []);

    const markAllAsRead = useCallback(async () => {
        try {
            // TODO: Replace with actual API call
            // await api.put('/notifications/read-all');
            
            setNotifications(prev => 
                prev.map(notification => ({ ...notification, read: true }))
            );
            
            toast.success('All notifications marked as read');
        } catch (err: any) {
            console.error('Error marking all notifications as read:', err);
            toast.error('Failed to mark all notifications as read');
        }
    }, []);

    const refreshNotifications = useCallback(() => {
        fetchNotifications();
    }, [fetchNotifications]);

    useEffect(() => {
        fetchNotifications();
    }, [fetchNotifications]);

    return {
        notifications,
        loading,
        error,
        markAsRead,
        markAllAsRead,
        refreshNotifications
    };
};